<template>

  <div style="display:bock;visibility:visible;">
    <l-map
      :zoom="zoom"
      :center="center"
      style="height: 800px; width: 100%;display:block;"
    >
      <l-tile-layer
        :url="url"
        :attribution="attribution"
       
      />
      <div  v-for="(coord,index) in map_coordinates" :key="index">
        <l-marker :lat-lng="[coord.lat,coord.lng]" :icon="icon" />
      </div>
    </l-map>
  </div>
</template>

<script>
import { LMap, LTileLayer, LMarker, LIcon } from "vue2-leaflet";
import { latLng, icon } from "leaflet";

export default {
  name: "Icon",
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LIcon
  },
  props:['map_coordinates'],
  data() {
    return {
      zoom: 15,
      center: latLng(6.453056,3.395833),
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      attribution:
        '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',

      icon: icon({
        iconUrl: "http://digitad.us-east-2.elasticbeanstalk.com/img/icon2.png",
        iconSize: [70, 82],
        iconAnchor: [16, 37]
      }),
      staticAnchor: [16, 37],
      customText: "Foobar",
      iconSize: 74
    };
  },
  computed: {
    dynamicSize() {
      return [this.iconSize, this.iconSize * 1.15];
    },
    dynamicAnchor() {
      return [this.iconSize / 2, this.iconSize * 1.15];
    }
  },
  methods: {}
};
</script>

<style>
.someExtraClass {
  background-color: aqua;
  padding: 10px;
  border: 1px solid #333;
  border-radius: 0 20px 20px 20px;
  box-shadow: 5px 3px 10px rgba(0, 0, 0, 0.2);
  text-align: center;
  width: auto !important;
  height: auto !important;
  margin: 0 !important;
}
</style>
